<template>
	<div id="pay" v-if="!util.isMajia() && !util.isQianfan()">
		<public-header title="支付收银台" :pagebacknum="pagebacknum"></public-header>
		<div class="content">
			<van-swipe :autoplay="3000" class="swiper" v-if="slides && slides.length > 0" indicator-color="#ff2d4b">
				<van-swipe-item v-for="(item, index) in slides" :key="index">
					<div @click="util.jsUrl(item.wxapp_link)">
						<img :src="item.thumb">
					</div>
				</van-swipe-item>
			</van-swipe>
			<div class="remain-time" v-if="order.pay_endtime && order.pay_endtime > 0">
				<div>支付剩余时间</div>
				<div class="in-clock">
					<count-down :endTime="order.pay_endtime"></count-down>
				</div>
			</div>
			<div class="order-summary flex-lr">
				<div class="left">
					<img :src="order.logo" alt="">
				</div>
				<div class="right">
					<div class="fee">
						<span class="underline">{{Lang.dollarSign}}{{order.fee}}</span>
					</div>
					<div class="order-info">
						<span class="underline">{{order.title}}</span>
					</div>
				</div>
			</div>
			<div class="bolck-title">选择支付方式</div>
			<div class="pay-list">
				<template v-for="(item, index) in payment">
					<div class="pay-item van-hairline--bottom flex-lr"  @click="onSelectPayType(item.value)">
						<div class="item-inner flex" v-if="is_rmpay">
							<img src="static/img/wx-icon.png" alt="" v-if="item.value == 'WECHATPAY_MY'">
							<img src="static/img/wx-icon.png" alt="" v-else-if="item.value == 'WECHATPAY_CN'">
							<img src="static/img/zfb-icon.png" alt="" v-else-if="item.value == 'ALIPAY_CN'">
							<img src="static/img/money-icon.png" alt="" v-else-if="item.value == 'credit'">

							<img src="static/img/wallet-presto.png" alt="" v-else-if="item.value == 'PRESTO_MY'">
							<img src="static/img/wallet-boost.png" alt="" v-else-if="item.value == 'BOOST_MY'">
							<img src="static/img/wallet-grabpay.png" alt="" v-else-if="item.value == 'GRABPAY_MY'">
							<img src="static/img/wallet-tng.png" alt="" v-else-if="item.value == 'TNG_MY'">
							<img src="static/img/wallet-maybank-qr.png" alt="" v-else-if="item.value == 'MAYBANK_MY'">
							<img src="static/img/wallet-razerpay.png" alt="" v-else-if="item.value == 'RAZERPAY_MY'">
							<img src="static/img/wallet-mcash.png" alt="" v-else-if="item.value == 'MCASH_MY'">
							<img src="static/img/finishMeal-icon.png" alt="" v-else-if="item.value == 'GOBIZ_MY'">
							<img src="static/img/delivery-icon.png" alt="" v-else-if="item.value == 'delivery'">
							<img src="static/img/finishMeal-icon.png" alt="" v-else-if="item.value == 'finishMeal'">
							<div class="item-box">
								<div class="item-title">{{item.title}}</div>
								<div class="item-subtitle flex" v-if="item.value == 'credit'">
									当前账户余额:
									<span class="money"> {{Lang.dollarSign}}{{member.credit2 || 0.00}}</span>
								</div>
								<div class="item-subtitle flex" v-if="item.value == 'delivery'">
									线下当面交易，到店付款，货到付款
								</div>
							</div>
						</div>
						<div class="item-inner flex" v-else>
							<img src="static/img/wx-icon.png" alt="" v-if="item.value == 'wechat'">
							<img src="static/img/zfb-icon.png" alt="" v-else-if="item.value == 'alipay'">
							<img src="static/img/money-icon.png" alt="" v-else-if="item.value == 'credit'">
							<img src="static/img/delivery-icon.png" alt="" v-else-if="item.value == 'delivery'">
							<img src="static/img/peerpay-icon.png" alt="" v-else-if="item.value == 'peerpay'">
							<img src="static/img/finishMeal-icon.png" alt="" v-else-if="item.value == 'finishMeal'">
							<img src="static/img/finishMeal-icon.png" alt="" v-else-if="item.value == 'card'">
							<img src="static/img/finishMeal-icon.png" alt="" v-else-if="item.value.indexOf('card_') == 0">
							<div class="item-box">
								<div class="item-title">{{item.title}}</div>
								<div class="item-subtitle flex" v-if="item.value == 'wechat'">
									<span class="pay-recommend">推荐使用</span>
									微信支付,安全快捷
								</div>
								<div class="item-subtitle flex" v-if="item.value == 'alipay'">
									<span class="pay-recommend" >推荐使用</span>
									简单、安全、快速
								</div>
								<div class="item-subtitle flex" v-if="item.value == 'credit'">
									当前账户余额:
									<span class="money"> {{Lang.dollarSign}}{{member.credit2 || 0.00}}</span>
								</div>
								<div class="item-subtitle flex" v-if="item.value == 'delivery'">
									线下当面交易，到店付款，货到付款
								</div>
							</div>
						</div>
						<i class="icon" :class="{'icon-check': item.value == paytype}"></i>
					</div>
				</template>
			</div>
		</div>
		<div class="save-btn">
			<!--公众号订阅消息测试代码-->
			<!--<wx-open-subscribe @success="subSuccess" @error="subError" :template="subTemplateId" id="subscribe-btn" >
				<script type="text/wxtag-template">
					// 在vue框架里，根据官网提示用这种替换template
					<style>
						.sub-btn {
							width: 100%;
							border: none;
							padding: 15px 0;
							background: #ff2d4b;
							border-radius: 5px;
							color: #fff;
						}
					</style>
					<button class="sub-btn">确认支付 {{Lang.dollarSign}}{{order.fee}}</button>
				</script>
			</wx-open-subscribe>-->
			<van-button type="default" size="large" :disabled="submitDisabled" @click="onSubmit">确认支付 {{Lang.dollarSign}}{{order.fee}}</van-button>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	<div>
	<stripe-checkout
		ref="checkoutRef"
		:pk="stripe_info.publishableKey"
		:session-id="stripe_info.sessionId"
	/>
	</div>
	<!--<van-popup v-model="stripe_info.qrcode">
		<div id="qrcode" class=""></div>
		<dvi>使用微信扫码支付</dvi>
	</van-popup>-->
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import countDown from '@/components/countDown'
	import h5app from '@/config/h5app'
	import {maJia} from '@/config/maJia'
	import { StripeCheckout } from '@vue-stripe/vue-stripe';
	import QRcode from 'qrcodejs2';
	export default {
		name: "pay",
		components: {
			PublicHeader,
			countDown,
			StripeCheckout
		},
		data() {
			return {
				showPreLoading: true,
				order_type: 'takeout',
				slides: [],
				order: {},
				payment: [],
				paytype: '',
				member: {},
				submitDisabled: false,
				is_rmpay: false,
				rmpay_info: {},
				is_stripe: false,
				//页面返回层数，stripe支付返回无线循环
				pagebacknum: 1,
				stripe_info: {
					publishableKey: '',
					sessionId: '',
					clientReferenceId: '',
					qrcode:false
				},
				subTemplateId: ''
			}
		},
		methods: {
			onLoad() {
				var that = this;
				this.util.request({
					url: 'system/paycenter/pay',
					method: 'POST',
					data: {
						sid: this.sid,
						id: this.order_id,
						order_type: this.order_type,
						type: 1
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					/*var templateIds = that.util.getStorage('templateIds');
					console.log('公众号订阅消息模板Ids', templateIds)
					if(templateIds.order) {
						that.subTemplateId = templateIds.order;
					}*/
					if(that.util.isMajia()) {
						maJia().then(function() {
							var payinfo = window.majia.payinfo;
							mag.pay(payinfo, function(data){
								that.util.$toast('订单支付成功', payinfo.url_detail);
							}, function(data){
								that.util.$toast('支付失败,具体原因：' + data, payinfo.url_pay);
							});
						});
					} else if(that.util.isQianfan()) {
						var payinfo = window.qianfan.payinfo;
						var QFdata = {
							'type' : parseInt(payinfo.type),
							'item' : payinfo.item,
							'send_type' : parseInt(payinfo.send_type),
							'address' : payinfo.address,
							'allow_pay_type' : parseInt(payinfo.allow_pay_type)
						};
						function QFH5ready() {
							QFH5.createOrder(QFdata['type'], QFdata['item'], QFdata['send_type'], QFdata['address'], QFdata['allow_pay_type'], function(state, data){
								if(state == 1){
									var order_id = data.order_id;
									that.util.request({
										url: 'system/paycenter/sync/qianfan',
										data:{
											tid: payinfo.tid,
											order_id: order_id
										}
									}).then((res) => {
										QFH5.jumpPayOrder(order_id, function(state, data){
											if(state == 1){
												that.util.$toast('订单支付成功', payinfo.url_detail);
											} else {
												that.util.$toast(data.error, payinfo.url_pay);
											}
										});
									})
								} else {
									that.util.$toast('创建交易订单失败:' + data.error, payinfo.url_pay);
								}
							});
						}
						QFH5ready();
					} else {
						this.slides = result.message.slides;
						this.payment = result.message.payment;
						result.message.order = this.util.df(result.message.order, 2);
						this.order = result.message.order;
						this.member = result.message.member;
						this.is_rmpay = result.message.is_rmpay;
						if(this.is_rmpay) {
							this.rmpay_info = result.message.rmpay;
						}
						this.is_stripe = result.message.is_stripe;
						if(this.payment.length > 0) {
							var default_paytype = this.payment[0]['value'];
							for(var i = 0,len = this.payment.length; i < len; i++){
								if(this.payment[i]['value'] == 'wechat') {
									default_paytype = 'wechat'
								}
							}
							this.paytype = default_paytype;
						}

						let autoPay = this.util.getUrlParam(window.location.href, 'autoPay');
						if(autoPay) {
							this.paytype = autoPay;
							this.onSubmit();
						}
					}
				});
			},

			onSelectPayType(paytype) {
				this.paytype = paytype;
				if(this.paytype == 'credit') {
					if(!this.member.credit2) {
						this.member.credit2 = 0;
					}
					if(this.member.credit2 - this.order.fee < 0) {
						this.submitDisabled = true;
					}
				} else {
					this.submitDisabled = false;
				}
			},

			onSubmit() {
				if(!this.paytype) {
					this.$toast('请先选择支付方式');
					return false;
				}

				if(this.util.ish5app() && (this.paytype == 'wechat' || this.paytype == 'alipay')) {
					h5app.init({});
					h5app.pay(this.paytype, '', '');
					return false;
				}
				this.submitDisabled = true;
				//开启马来支付，使余额货到付款还是走之前的
				if(this.is_rmpay && (this.paytype != 'credit' && this.paytype != 'delivery')) {
					if(this.rmpay_info.baseurl) {
						this.$toast.loading({
							message: 'pay waiting...',
							forbidClick: true,
						});
						window.location.href = this.rmpay_info.baseurl + this.paytype;
					} else {
						this.$toast(this.rmpay_info.error);
						this.submitDisabled = false;
						return false;
					}
				} else if(this.is_stripe && (this.paytype == 'card' || this.paytype.indexOf('card_') == 0)) {
					let that = this
					if(this.paytype.indexOf('card_') == 0) {
						this.util.stripepay({
							pay_type: this.paytype,
							order_type: this.order_type,
							order_id: this.order_id,
							extra: this.extra,
							vue: this
						});
					} else {
						this.util.request({
							url: 'system/paycenter/pay',
							method: 'POST',
							data: {
								pay_type: this.paytype,
								order_type: this.order_type,
								id: this.order_id
							},
							showLoading: true
						}).then((res) => {
							let result = res.data.message;
							if (result.errno) {
								this.$toast(result.message);
								return false;
							}
							if (this.paytype == 'wechat') {
								this.stripe_info.qrcode = true
								let qrcode = new QRcode('qrcode', {
									width: 150,
									height: 150, // 高度
									text: result.message.wechat.qr_code_url, // 二维码内容
									image: ''
								});
							} else if(this.paytype == 'card') {
								this.stripe_info.publishableKey = result.message.publishableKey
								this.stripe_info.sessionId = result.message.sessionid
								// this.stripe_info.clientReferenceId = result.message.clientReferenceId
								that.$refs.checkoutRef.redirectToCheckout();
							}
						})
					}
				}
				else {
					this.util.pay({
						pay_type: this.paytype,
						order_type: this.order_type,
						order_id: this.order_id,
						extra: this.extra,
						vue: this
					});
				}
			},

			subSuccess(res) {
				var that =  this;
				console.log('订阅成功', res);
				that.onSubmit();
			},

			subError(res) {
				var that =  this;
				console.log('订阅失败', res);
				that.onSubmit();
			}

		},
		mounted () {
			this.onLoad();
		},
		created () {
			this.query = this.$route.query;
			if(this.query) {
				this.order_id = this.query.order_id || this.query.id;
				if(!this.query.order_id) {
					this.order_id = this.query.id;
				}
				if(this.query.stripe_back == 1) {
					this.pagebacknum = 1000;
				}
				this.order_type = this.query.order_type;
				this.extra = this.query.extra;
			}
		}
	}
</script>

<style>
	#pay .content{
		position: absolute;
		left: 0;
		bottom: 70px;
		right: 0;
		background-color: #f8f8f8;
	}
	#pay .content .swiper{
		width: 100%;
		height: 80px;
	}
	#pay .content .swiper img{
		width: 100%;
		height: 100%;
		pointer-events: none;
	}
	#pay .content .remain-time{
		color: #666;
		text-align: center;
		padding: 15px 0;
		background: #f9f9fc;
		font-size: 10px;
		line-height: 1.5;
	}
	#pay .content .remain-time .in-clock{
		margin-top: 5px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#pay .content .remain-time .in-clock span{
		background-color: #000;
		color: #fafafa;
		font-size: 12px;
		padding: 3px 3px;
		border-radius: 3px;
		margin: 0 3px;
	}
	#pay .order-summary{
		padding: 15px 0;
		margin-left: 0;
		background: #f5f5f5  url(../../../static/img/line.png) repeat-x 0 0;
		background-size: auto 100%;
	}
	#pay .order-summary .left{
		width: 30%;
		display: flex;
		justify-content: center;
	}
	#pay .order-summary .left img{
		width: 50px;
		height: 50px;
		border-radius: 50%;
	}
	#pay .order-summary .right{
		width: 70%;
		line-height: 22px;
	}
	#pay .fee{
		font-size: 16px;
	}
	#pay .order-info{
		font-size: 12px;
	}
	#pay .underline{
		border-bottom: 1px dotted #e0e0e0;
	}
	#pay .bolck-title{
		margin: 10px 15px;
		color: #6d6d72;
		font-size: 14px;
	}
	#pay .pay-list{
		background-color: #fff;
		padding-left: 15px;
	}
	#pay .pay-list .pay-item{
		padding: 10px 15px 10px 0;
	}
	#pay .pay-list .item-inner img{
		width: 40px;
		height: 40px;
		margin-right: 10px;
	}
	#pay .pay-list .item-inner .item-title{
		font-size: 14px;
		color: #333;
	}
	#pay .pay-list .item-inner .item-subtitle{
		margin-top: 5px;
		color: #999;
		font-size: 12px;
		white-space: normal;
	}
	#pay .pay-list .item-inner .item-subtitle .money{
		font-size: 14px;
		color: #ff2d4b;
	}
	#pay .pay-list .item-inner .item-subtitle .pay-recommend{
		color: #ff9c05;
		padding: 2px;
		margin-right: 5px;
		border: 1px solid #ff9c05;
		border-radius: 4px;
		font-size: 10px;
	}
	#pay .pay-list .pay-item .icon{
		width: 20px;
		height: 20px;
		border-radius: 50%;
		border: 1px solid #ccc;
		text-align: center;
		line-height: 18px;
	}
	#pay .pay-list .pay-item .icon.icon-check{
		color: #fff;
		background-color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#pay .save-btn{
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 10px;
		background-color: #fff;
	}
	#pay .save-btn .van-button{
		background-color: #ff2d4b;
		color: #fff;
	}
	#pay .save-btn .van-button.disabled{
		color: #999;
		background-color: #eee;
		border: 1px solid #e5e5e5;
	}
	#pay #qrcode{
		margin: 20px auto;
	}
</style>



